}


.Navbar_name__3nKwt:hover{
    color: #002c5d;
}

.Navbar_top_name__1akWT:hover{
    color: #fff;
}

.Navbar_hamburger_button__206xo{
    width: 2em;
    height: 19.6px;
    position: relative;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
    z-index: 10;
}


.Navbar_hamburger_button__206xo > span{
    width: 100%;
    border:0px;
    height: 3px;
    border-radius: 15px;
    background: #0078ff;
    opacity: 1;
    display: block;
    position: absolute;
    transform: rotate(0deg);
    transition: .45s ease-in-out;  
}

/* 
.top_hamburger_button > span{
    background: #FFF;
} */

.Navbar_hamburger_button__206xo > span:nth-child(1){
    top:0;
}


.Navbar_hamburger_button__206xo > span:nth-child(2){
    top:3px;
    margin: 4.8px 0;
}

.Navbar_hamburger_button__206xo > span:nth-child(3){
    top:15.6px;
}

.Navbar_open_button__axx5t span:nth-child(1){
    transform: rotate(135deg);
    top:7.8px;
}

.Navbar_open_button__axx5t span:nth-child(2){
    opacity: 0;
}

.Navbar_open_button__axx5t span:nth-child(3){
  top:7.8px;
  transform: rotate(-135deg);
}

.Navbar_menu__NKpxG{
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: absolute;
    top: 60px;
    background-color: #fff;
    left: 0;
    padding: 0 7%;
    transition: visibility 0s ease-in-out, opacity 0.5s ease-in-out, background-color 2s ease-in-out,transform 1s ease-in-out;
    transform: translateY(-150%);
}

.Navbar_open_menu__1kCv6{
    transform: translateY(0%);
    display: flex;
    visibility: visible;
    opacity: 1;
}


.Navbar_items__2UnO7{
    display: flex;
